#container {
    box-shadow: 0 10px 20px 1px rgb(4, 0, 130);
    /* background: rgba(255, 255, 255, 0.90); */
    text-align: center;
    border-radius: 5px;
    overflow: hidden;
    margin: 3em auto;
    height: 350px;
    width: 600px;
}
.product-details {
    position: relative;
    text-align: left;
    overflow: hidden;
    padding: 30px;
    height: 100%;
    float: left;
    width: 50%;
}

#container .product-details h1 {
    font-family: 'Bebas Neue', cursive;
    display: inline-block;
    position: relative;
    font-size: 30px;
    color: #344055;
    margin: 0;

}
#container .product-details h1:before {
    position: absolute;
    content: '';
    right: 0%;
    top: 0%;
    -webkit-transform: translate(25px, -15px);
            transform: translate(25px, -15px);
    font-family: 'Bree Serif', serif;
    display: inline-block;
    /* background: #ffe6e6; */
    border-radius: 5px;
    font-size: 14px;
    padding: 5px;
    color: white;
    margin: 0;
    -webkit-animation: chan-sh 6s ease infinite;
            animation: chan-sh 6s ease infinite;
}
.hint-star {
    display: inline-block;
    margin-left: 0.5em;
    color: gold;
    width: 50%;
}
#container .product-details>p {
    font-family: 'EB Garamond', serif;
    text-align: center;
    font-size: 18px;
    color: #7d7d7d;
}
.control {
    position: absolute;
    bottom: 10%;
    left: 22.8%;
}
.btn {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    transition: 0.3s linear;
    background: #809fff;
    border-radius: 5px;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    outline: none;
    border: none;
    color: #eee;
    padding: 0;
    margin: 0;

}

.btn:hover {
    -webkit-transform: translateY(-6px);
            transform: translateY(-6px);
    background: #1a66ff;
}

.btn span {
    font-family: 'Farsan', cursive;
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
    display: inline-block;
    padding: 10px 20px;
    font-size: 1.2em;
    margin: 0;

}

.btn .price,
.shopping-cart {
    background: #333;
    border: 0;
    margin: 0;
}

.btn .price {
    -webkit-transform: translateX(-10%);
            transform: translateX(-10%);
    padding-right: 15px;
}

.btn .shopping-cart {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    position: absolute;
    background: #333;
    z-index: 1;
    left: 0;
    top: 0;
}

.btn .buy {
    z-index: 3;
    font-weight: bolder;
}

.btn:hover .price {
    -webkit-transform: translateX(-110%);
            transform: translateX(-110%);
}

.btn:hover .shopping-cart {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
}



.product-image {
    transition: all 0.3s ease-out;
    display: inline-block;
    position: relative;
    overflow: hidden;
    height: 100%;
    float: right;
    width: 45%;
    display: inline-block;
}

#container img {
    width: 100%;
    height: 100%;
}

.info {
    background: rgba(27, 26, 26, 0.9);
    font-family: 'Bree Serif', serif;
    transition: all 0.3s ease-out;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    position: absolute;
    line-height: 1.8;
    text-align: left;
    font-size: 105%;
    cursor: no-drop;
    color: #FFF;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
}

.info h2 {
    text-align: center
}

.product-image:hover .info {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.info ul li {
    transition: 0.3s ease;
}

.info ul li:hover {
    -webkit-transform: translateX(50px) scale(1.3);
            transform: translateX(50px) scale(1.3);
}

.product-image:hover img {
    transition: all 0.3s ease-out;
}

.product-image:hover img {
    -webkit-transform: scale(1.2, 1.2);
            transform: scale(1.2, 1.2);
}
